<template>
  <div>
    <div class="bg-base2 p2 flex-row">
      <a class="uppercase" @click="close"><i class="fas fa-chevron-left m1-right"></i>back</a>
      <div class="flex-one"></div>
      <a @click="shareDialog = true" class="contrast m3-right"><i class="fas fa-share-square" /></a>
      <div class="flex-row align-center">
        <a class="m1-right" @click="back" :disabled="page === 1"><i class="fas fa-chevron-left"></i></a>
        <p class="tertiary font-2">Page {{page}}</p>
        <a class="m1-left" @click="forward" :disabled="page === pages"><i class="fas fa-chevron-right"></i></a>
      </div>
    </div>
    <pdf :src="pdf" :page="page" @num-pages="setNumberOfPages"></pdf>

    <transition name="fade-in">
      <div v-if="shareDialog" class="cover absolute bg-base2 z3 p2">
        <div class="p3 bg-base shadow rounded">
          <p class="uppercase tertiary p2-bottom m3-bottom border-bottom-base2"><a @click="shareDialog = false"><i class="fas fa-times m1-right"/> Cancel</a></p>
          <p class="m1-bottom">Email to:</p>
          <div class="flex-row align-center">
            <input class="input input-tertiary flex-one m2-bottom" type="email" placeholder="email address" v-model="shareEmail">
            <p class="tertiary2 p2-bottom border-bottom-tertiary"><a @click="shareEmail = undefined"><i class="fas fa-times" /></a></p>
          </div>
          <div class="text-right">
            <button class="button alt uppercase" :disabled="!shareEmail" @click="shareFile">share</button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import Pdf from 'vue-pdf'
import { mapGetters } from 'vuex'

export default {
  name: 'PdfViewer',
  data () {
    return {
      page: 1,
      pages: 0,
      shareDialog: false,
      shareEmail: undefined
    }
  },
  computed: {
    ...mapGetters(['pdf', 'session'])
  },
  components: { Pdf },
  inject: ['userClient'],
  methods: {
    setNumberOfPages (n) {
      this.pages = n
    },
    close () {
      this.$router.back()
    },
    back () {
      if (this.page === 1) return
      this.page--
    },
    forward () {
      if (this.page === this.pages) return
      this.page++
    },
    shareFile () {
      const key = this.pdf.split('/').pop()
      this.userClient.shareFile(this.session.id, key, this.shareEmail)
        .catch(e => window.alert('Error sharing file. Try again.'))
      this.shareDialog = false
    }
  },
  mounted () {
    this.shareEmail = this.session.email
  }
}
</script>
