<template>
  <div>
    <div class="flex-row p2 m2-bottom">
      <div class="primary" @click="$router.back()"><i class="fas fa-chevron-left p1-right"></i> <span>BACK</span></div>
      <div class="flex-one"></div>
      <div class="font-title">PROPERTIES</div>
    </div>

    <div class="flex-column">
      <div v-for="(listing, i) in listings"
        :key="i"
        class="listing flex-column m1-bottom"
        :style="{backgroundImage: image(listing)}"
        @click="detailedListing = listing">
        <div class="flex-one"></div>
        <div class="flex-row">
          <div class="details flex-column text-shadow">
            <p class="font3 font-bold">{{listing.UnparsedAddress}}</p>
            <p>{{listing.ListPrice | currency}}</p>
          </div>
        </div>
      </div>
    </div>

    <transition name="fade-in">
      <div v-if="detailedListing" class="fixed z2 cover bg-sheen">
        <div class="absolute prompt flex-column rounded bg-base p2 shadow scrolly">
          <div class="flex-row m2-bottom">
            <div class="tertiary" @click="detailedListing = null"><i class="fas fa-times p1-right"></i> <span>CLOSE</span></div>
            <div class="flex-one"></div>
          </div>

          <div class="flex-one scrolly">
            <p class="font3 font-bold">{{detailedListing.UnparsedAddress}}, {{detailedListing.City}}</p>
            <p class="secondary m2-bottom">{{detailedListing.ListPrice | currency }}</p>

            <img class="h-fill m2-bottom" :src="detailedListingImage">

            <div class="flex-row scrollx m2-bottom">
              <img class="thumbnail" v-for="(image, i) in detailedListing.Images" :key="i" :src="image.MediaURL" @click="detailedListingImage = image.MediaURL">
            </div>

            <!-- <p class="listing-description m2-bottom">{{detailedListing.PublicRemarks}}</p> -->
            <p><span class="secondary">Bedrooms:</span> {{detailedListing.BedroomsTotal}}</p>
            <p><span class="secondary">Bathrooms:</span> {{bathrooms}}</p>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
export default {
  name: 'Listings',
  data () {
    return {
      listings: [],
      detailedListing: null,
      detailedListingImage: null
    }
  },
  props: ['show'],
  computed: {
    bathrooms () {
      return `${this.detailedListing.BathroomsFull} Full` +
        (this.detailedListing.BathroomsHalf ? `, ${this.detailedListing.BathroomsHalf} Half` : '')
    }
  },
  watch: {
    detailedListing () {
      if (this.detailedListing) {
        this.detailedListingImage = this.detailedListing.Images[0].MediaURL
      }
    }
  },
  inject: ['listingsClient'],
  filters: {
    currency (value) {
      var formatter = new Intl.NumberFormat('en-US', {
        style: 'currency',
        currency: 'USD',
        minimumFractionDigits: 0
      })
      return formatter.format(value)
    }
  },
  methods: {
    image (listing) {
      const url = listing.Images[0].MediaURL
      console.log('IMAGE', url)
      return `url(${url})`
    }
  },
  mounted () {
    const self = this
    this.listingsClient.getListings()
      .then(listings => {
        self.listings = listings
      })
  }
}
</script>

<style lang="scss" scoped>
.listing {
  height: 180px;
  background-position: center;
  background-size: cover;
  .details {
    background: linear-gradient(270deg, rgba(0, 0, 0, 0.2), rgba(0, 0, 0, 0.7));
    color: white;
    padding: 4px 18px 4px 8px;
    border-top-right-radius: 16px;
  }
}
.listing-description {
  max-height: 250px;
  overflow-y: auto;
  white-space: pre-wrap;
}
.thumbnail {
  height: 60px;
  margin-right: 4px;
}
</style>
