<template>
  <div>
    <div class="p3 flex-row">
      <a class="tertiary font-title uppercase" @click="signOut"><i class="fas fa-sign-out-alt fa-rotate-180 m1-right"></i>Logout</a>
      <div class="flex-one"></div>
      <button class="button alt small" @click="addClient"><i class="fas fa-plus m1-right"></i>CLIENT</button>
    </div>

    <div class="p3 p1-top">
      <div class="m5-bottom">
        <title-label title="CLIENTS"></title-label>
        <div v-for="client in openClients" :key="client.id" class="flex-row bg-secondary base p2 mhalf-bottom" @click="viewClient(client)">
          <p class="flex-one">{{ client.name }}</p>
          <span class="tertiary"><i class="fas fa-chevron-right" /></span>
        </div>
        <p v-if="hasNoClients" class="tertiary">No active clients</p>
      </div>

      <div class="m5-bottom">
        <title-label v-if="closedClients.length" title="CLOSED"></title-label>
        <div v-for="client in closedClients" :key="client.id" class="p2-top p2-bottom border-bottom-base2">
          <p class="tertiary">{{ client.name }}</p>
        </div>
      </div>

      <div class="p3"></div>

      <div class="m5-bottom">
        <title-label title="admins" />
        <div v-for="admin in admins" :key="admin.id" class="flex-row align-center p2-top p2-bottom border-bottom-base2">
          <div class="flex-one">
            <p>{{ admin.name }}<span v-if="session.email === admin.email" class="tertiary"> (You)</span></p>
            <p class="tertiary font-1 p1-top">{{admin.email}}</p>
          </div>
          <div class="error" @click="deleteUser(admin)">
            <i class="fas fa-trash"></i>
          </div>
        </div>
      </div>
    </div>

    <transition name="slide-up">
      <div v-if="prompt" class="prompt fixed scrolly bg-base z2 shadow p3 flex-column">
        <div class="m4-bottom text-right">
          <a class="tertiary font-title" @click="prompt = false"><i class="fas fa-times m1-right"></i>CANCEL</a>
        </div>

        <create-client class="flex-one" @done="clientCreated"></create-client>
      </div>
    </transition>

    <transition name="fade-in">
      <div v-if="confirmingDelete" class="fixed cover bg-base z3 flex-column align-center flex-center">
        <div class="p2 bg-base shadow rounded">
          <h3 class="m4-bottom">Permanently delete this user?</h3>
          <div class="text-right">
            <a class="primary p2 m2-right" @click="deleteUser(confirmingDelete)">delete</a>
            <button class="button bg-primary contrast uppercase" @click="confirmingDelete = undefined">cancel</button>
          </div>
        </div>
      </div>
    </transition>
  </div>
</template>

<script>
import { mapActions, mapGetters } from 'vuex'
import TitleLabel from '@/components/TitleLabel'
import CreateClient from '@/views/forms/CreateClient'

export default {
  name: 'Realtor',
  components: { TitleLabel, CreateClient },
  data () {
    return {
      prompt: false,
      confirmingDelete: null,
      admins: []
    }
  },
  inject: ['realtorClient'],
  computed: {
    ...mapGetters(['session', 'clients']),
    hasNoClients () {
      return this.clients.length === 0
    },
    openClients () {
      return this.clients.filter(c => c.status !== 'closed')
    },
    closedClients () {
      return this.clients.filter(c => c.status === 'closed')
    }
  },
  methods: {
    ...mapActions(['setSession', 'setClients']),
    signOut () {
      this.setSession(undefined)
      this.$router.push('/')
    },
    addClient () {
      this.prompt = true
    },
    viewClient (client) {
      this.$router.push(`/realtor/client/${client.id}`)
    },
    clientCreated () {
      this.prompt = false
      this.refreshClients()
    },
    deleteUser (user) {
      if (this.confirmingDelete) {
        this.realtorClient.deleteUser(user.id, this.session)
          .then(() => {
            this.refreshClients()
            this.confirmingDelete = null
          })
      } else {
        this.confirmingDelete = user
      }
    },
    refreshClients () {
      this.realtorClient.getUsers(this.session)
        .then(users => {
          const clients = users.filter(u => u.role !== 'realtor')
          this.setClients(clients)
          this.admins = users.filter(u => u.role === 'realtor')
        })
    }
  },
  mounted () {
    this.refreshClients()
  }
}
</script>

