const optionsKeys = (promptsResolver, optionsId) => promptsResolver().find(p => p.id === optionsId).options.map(o => o.key)

const Yes = (key) => responses => responses[key] === 'Yes'
const anyYes = (arg1, arg2) => Array.isArray(arg1) ? anyYesArray(arg1) : anyOptionYes(arg1, arg2)

const anyYesArray = (keys) => responses => keys.find(k => responses[k] === 'Yes') !== undefined
const anyOptionYes = (promptsResolver, optionsId) =>
  responses => optionsKeys(promptsResolver, optionsId).find(k => responses[k] === 'Yes') !== undefined

const hasText = (key) => responses => (!!responses[key] && responses[key].length)

export { Yes, anyYes, hasText, optionsKeys }
