import Vue from 'vue'
import App from './App.vue'
import router from './router'
import store from './store'
import AnimatedVue from 'animated-vue'
import 'animate.css/animate.css'
import ValueTransition from '../libs/tentkeep-vue/src/components/ValueTransition'

Vue.use(AnimatedVue)
Vue.component('value-transition', ValueTransition)

Vue.config.productionTip = false

Vue.mixin({
  methods: {
    showModal (component, props) {
      const appRoot = this.$root.$children[0]
      appRoot.modal = component
      appRoot.modalProps = props
    }
  }
})

new Vue({
  router,
  store,
  render: h => h(App)
}).$mount('#app')
